.itworks {
  background-color: #fff;
  border-top: 1px solid #11111B;
  padding-top: 50px;
  padding-bottom: 30px;
  overflow: hidden;

  @media #{$lg} {
    padding-top: 150px;
    padding-bottom: 150px;
  }

  &_wrap {
    max-width: 100%;
    margin: 0 auto;
    padding: 0 20px;

    @media #{$lg} {
      max-width: 1120px;
      padding: 0;
    }
  }

  .headline {
    max-width: 100%;

    @media #{$lg} {
      max-width: 100%;
    }

    &:before {
      content: 'It Works.';
    }
  }
}
